// Function to read all search parameters from the URL
function getSearchParams() {
    var params = new URLSearchParams(window.location.search);
    var paramsObject = {};

    params.forEach(function(value, key) {
        paramsObject[key] = value;
    });

    return paramsObject;
}

// Function to create a new URL based on all search parameters
function createNewURL(params) {
    var baseURL = 'https://qualys-html-to-pdf-0d3555d590c9.herokuapp.com/generate-pdf';
    var newURL = baseURL + '?' + new URLSearchParams(params).toString();
    return newURL;
}

// Function to set the href attribute of the anchor element
function setAnchorHref(newURL) {
    var anchorElement = document.querySelector('.report');
    anchorElement.href = newURL;
}

document.addEventListener('DOMContentLoaded', function() {
    // Get all search parameters from the URL
    var searchParams = getSearchParams();

    // Create a new URL based on all search parameters
    var newURL = createNewURL(searchParams);

    // Set the href attribute of the anchor element
    setAnchorHref(newURL);
});